.page {
  font-family: Manrope, sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: $main-dark-blue;

  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
    outline: 1px solid red;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }
  }

  &:has(.page__menu:target) {
      overflow: hidden; /* not to scroll the page */
  }
}


