.section-title {
  margin: 0;
  margin-bottom: 48px;

  font-weight: 700;
  font-size: 32px;
  line-height: 32px;
  letter-spacing: 0px;

  @include onTablet {
    font-size: 40px;
    line-height: 40px;
    margin-bottom: 56px;
  }

  @include onDesktop {
    font-size: 48px;
    line-height: 48px;
    margin-bottom: 56px;
  }
}
